.Contactpage{
    width: 100%;
    height: 100vh;
    overflow-y: hidden;
}
.Contactpage-container{
    display: flex;
    width: 100%;
    height: 100%;
    flex: 1;
}
.Contactpage-container-section1{
    flex: 0.5;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.Contactpage-container-section1 img{
    width: 100%;
    height:100%;
}

.Contactpage-container-section2{
    flex: 0.5;
    display: flex;
    flex-direction: column;
    background-color: #a2454f;
    color: white;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #d0878f;

}
.smallpic{
    width: 80%;
    height: 40%;
}
.Contactpage-container-section2 img{
    width: 25%;
    align-self: flex-end;
}
.Contactpage-container-section2-content{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    width: 80%;
}
.Contactpage-data{
    color: rgb(249, 224, 224);
}
.Contactpage-data h4{
    font-size: 22px;
    font-weight: 300;
    line-height: 30px;
}
.Contactpage-data p{
    font-size: 19px;
    font-weight: 300;
    line-height: 25px;
}
.Contactpage-linklist{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}
.Contactpage-linklist ul li a{
    color: rgb(249, 224, 224);
    font-size: 20px;
    font-weight: 400;
}
