/* Second.css */

.container {
    display: flex;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-color: #f7e6d9;
    opacity: 0; /* Initial opacity */
    transform: translateX(50px); /* Initial position off-screen */
    transition: opacity 1s ease, transform 2s ease;
    border-bottom: 1px solid white;
}
.container.animate {
    opacity: 1; /* Opacity when in view */
    transform: translateX(0); /* Move to the original position */
  }
.right {
    flex: 0.5;
    color: #111;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;

}
.smallpic {
    width: 60%;
    display: flex;
    flex-direction: column-reverse;
}
.smallpic img {
    width: 60%;
    align-self: center;
    object-fit: contain;
}

.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    
}

.heading {
    font-size: 25px;
    font-weight: 400;
    line-height: 27px;
    width: 80%;
}

.para{ 
    font-size: 16px;
    font-weight:350 ;
    line-height: 23px;
    width: 80%;
}

.left {
    flex: 0.5;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
}
.left-two{
    width: 70%;
    display: flex;
    justify-content: center;
    height: 80%;
    overflow-y: hidden;
}
.two{
    width: 65%;
    object-fit: contain;
}
.left-three{
    width: 50%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    margin-bottom: 10px;
}
.three{
    width: 60%;
}
@media screen and (max-width: 650px) {
    .container{
        flex-direction: column;
    }
    .right{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .content{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .smallpic img{
        display: none;
    }
    .content .heading{
        margin: 0;
        line-height: 23px;
        font-size: 30px;
    }
    .para{
        font-size: 15px;
        line-height: 20px;
    }

    .left{
        flex-direction: row;
    }
    .left-two{
        display: flex;
        align-items: center;
        justify-content: center;
        
    }
    .two{
        height: 100%;
        width: 100%;
    }
    .left-three{
        display: flex;
        align-items: center;
        justify-content: center;
        
    }
    .three{
        height: 100%;
        width: 100%;
    }
}
@media screen and (max-width: 783px) {
    
    .content{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .content .heading{
        font-size: 20px;
    }
    .para p{
        font-size: 20px;
    }
    .left-two > img{
        width: 65%;
    }
    .left-three > img{
        width: 60%;
    }
}
@media screen and (max-width: 890px) {

    .right,
    .left {
        flex: 1;
    }
    .smallpic {
        width: 60%;
    }
    .heading{
        font-size: 24px;
        /* line-height: 24px; */
    }
    .left-two > img{
        width: 65%;
    }
    .left-three > img{
        width: 60%;
    }
}

