.Fourth-heading h3{
    font-size: 18px;
    font-weight: 350;
    margin-left: 10px;
    margin-top: 50px;
    color: #111;
}
section {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Fourth{
    width: 100vw;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #f7e6d9;
}
.Fourth-grid{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.Fourth-grid-1,
.Fourth-grid-2,
.Fourth-grid-3,
.Fourth-grid-4{
    flex: 0 0 calc(25% - 20px);
    max-width:0 0 calc(25% - 20px) ;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.Fourth-grid-1 img,
.Fourth-grid-2 img,
.Fourth-grid-3 img,
.Fourth-grid-4 img {
    width: 100%;
    
}
.Fourth-grid-1-content ,
.Fourth-grid-2-content ,
.Fourth-grid-3-content ,
.Fourth-grid-4-content {
    width: 100%;
}

.Fourth-grid-1-content h4,
.Fourth-grid-2-content h4,
.Fourth-grid-3-content h4,
.Fourth-grid-4-content h4{
    font-size: 22px;
    font-weight: 400;
    line-height: 30px;
    color: #111;
}
.Fourth-grid-1-content p,
.Fourth-grid-2-content p,
.Fourth-grid-3-content p,
.Fourth-grid-4-content p{
    font-size: 15px;
    line-height: 22px;
    color: #111;
}

@media screen and (max-width: 600px) {
    .Fourth-grid-1,
    .Fourth-grid-2,
    .Fourth-grid-3,
    .Fourth-grid-4 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

@media screen and (max-width: 968px) {
    .Fourth-grid-1,
    .Fourth-grid-2,
    .Fourth-grid-3,
    .Fourth-grid-4 {
        flex: 0 0 calc(50% - 20px);
        max-width: 0 0 calc(50% - 20px);

    }
}