/* App.css */
.app-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    
}

.left-sidebar {
    /* width: 200px; */
    flex: 0.5;
    background-color: #a2454f;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
}

.left-sidebar ul {
    list-style: none;
    margin: 70px 50px;
}
.left-sidebar ul li{
    padding: 20px;
}
.MenuPage-nav-links{
    text-decoration: none;
    color: white;
    font-size: 40px;
    font-weight: 400;
    line-height: 60px;
}
.MenuPage-nav-links:hover{
    color: rgb(212, 207, 207);
}

.right-side {
    position: relative;
    flex: 0.5;
    background-color: white;
    width: 50%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.right-side img {
    width: 100%;
    height: auto;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 0;
    padding: 15px 20px;
    background-color: white;
    border: none;
    cursor: pointer;
}

.close-button{
    font-size: 30px;
}

@media screen and (max-width:650px) {
    .left-sidebar{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .right-side{
        background-color: #a2454f;
    }
    .right-side img{
        display: none;
    }  
    .close-button{
        background-color: #a2454f;
        color: white;
    } 
}
