.Third-section{  
    height: 800px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f7e6d9;
    border-bottom: 1px solid white;
}
.con{
    display: grid;
    grid-template-columns: repeat(10 , 1fr);
    gap: 18px;
    overflow-x: auto;
    scrollbar-width: none;
}
.con::-webkit-scrollbar{
    display: none;
}
.i{
    width: 325px;
    height: 450px;
    object-fit: cover;
}
.image1{
    width: 850px;
}
.image2{
    width: 400px;
}
