.slider-wrapper {
    position: relative;
}
.slider-wrapper .arw{
    width: 50px;
    height: 50px;
    cursor: pointer;
    position:absolute;
    transform: translateY(-50%); 
    top: 50%; 
}
.arw#left{
    left: 30px;
    transform: rotate(180deg);
}
.arw#right{
    right: 30px;
}