.Fifth{
    width: 100vw;
    min-height: 100vh;
    background-color: #f7e6d9;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    opacity: 0; /* Initial opacity */
    transform: translateX(50px); /* Initial position off-screen */
    transition: opacity 1s ease, transform 2s ease;
  }
  
.Fifth.animate {
    opacity: 1; /* Opacity when in view */
    transform: translateX(0); /* Move to the original position */
  }
.Fifth-container{
    display: flex;
    justify-content:flex-start;
    align-items: center;
    flex: 1;
    
}
.Fifth-container-section-1{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: column;
    flex:0.5;
    width: 100%;
}
.Fifth-container-section-1-image img{
    margin-left: 40px; 
    width: 60%;
}
.Fifth-container-section-1-content {
    margin-left: 40px; 
    color: black;
    width: 80%;
}
.Fifth-container-section-1-content h3{
    font-size: 25px;
    font-weight: 400;
    line-height: 30px;
}
.Fifth-container-section-1-content p{
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;
}
.Fifth-container-section-2{
    flex:0.5;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.Fifth-container-section-2 img{
    width: 100%;
    height: 90vh;
    object-fit: cover;
}


@media screen and (max-width: 600px) {
    .Fifth-container{
        flex-direction: column;
    }
    .Fifth-container-section-1{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .Fifth-container-section-1-image img{
        width: 100%;
        margin-left: 0;
        margin-top: 20px;
    }
    .Fifth-container-section-1-content {
        margin-left: 0; 
    }
    .Fifth-container-section-2 img{
        object-fit: contain;
        /* margin-bottom: 20px; */
    }
}