.Footer{
    height:740px;
    width: 100%;
}

.Footer-container-1{
    height: 400px;
    width: 100%;
    background-color: #a2454f;
}
.Footer-container-section-1{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.Footer-logo-image{
    width: auto;
    display: flex;
    align-content: center;
    justify-content:flex-start;
}
.Footer-address-section{
    color: #e2d4ca;
}
.Footer-address-section h3{
    font-size: 28px;
    font-weight: 300;
    line-height:35px;
}
.Footer-address-section h4{
    font-size: 18px;
    font-weight: 400;
    line-height:20px;
}
.Footer-contact-info p:hover{
    color: #cbb4a3;  
}

.Footer-contact-info p{
    font-size:18px ;
    font-weight: 400;
    line-height: 22px;
    cursor: pointer;
    transition: color 0.3s ease;
    color: #e2d4ca;
}
.Footer-container-section-1-part2-border{
    background-color: #a2454f;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Footer-container-section-1-part2-border-line{
    border: 1px solid #bb626b;
    width: 90%;
    background-color: #a2454f;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Footer-container-section-1-part2{
    width: 100%;
    height: 150px;
    background-color: #a2454f;
    display: flex;
    justify-content: space-between;
    align-items: center;
   flex-direction: row;
}
.Footer-container-section-1-part2-links{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.Footer-container-section-1-part2-links ul{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
}
.Footer-container-section-1-part2-links ul li {
    font-size: 18px;
    font-weight: 400;
    line-height: 20px;
    cursor: pointer;
    color: #e2d4ca;
    transition: color 0.3s ease;
}
.Footer-container-section-1-part2-links ul li:hover{
    color: #cbb4a3;
}
.Footer-container-section-2 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 180px;
    gap: 30px;
    background-color: #f7e6d9;
    flex: 1;
}
.imag1{
    flex:0.5;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 90%;
    object-fit: contain;
}
.Footer-container-section-2-image1{
    width: 47%;
}
.imag2{
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex:0.5;
}
.Footer-container-section-2-image2{
    width: 50%;
}

@media  screen and (max-width:600px) {
    .Footer-logo-image{
        display: none;
    }
    .Footer-container-section-1{
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin-left: 20px;
    }
    .Footer-container-section-1-part2-links{
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
    }
    .Footer-container-section-1-part2-links ul{
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        gap: 20px;
        flex-direction: column;
        margin: 30px;
    }
    .Footer-container-section-2-image2{
        width: 70%;
    }
    .Footer-container-section-2-image1{
        width: 70%;
    }
}
@media  screen and (max-width:1000px) {
    .Footer-logo-image{
        display: none;
    }
    
}