.Navbar-section{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    top: 0;
    position: absolute;
    right: 0;
    left: 0;

}
.Navbar-items{
    display: flex;
    flex-direction: row;
}
.items {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    cursor: pointer;
    color: white;
    transition: color 0.5s;
    font-size: 20px;
    font-weight: 400;
    gap: 10px;
    margin-top: 10px;
}
.nav-links{
    text-decoration: none;
}
.items:hover{
    color: rgb(202, 197, 197);
}
.items p{
    font-size: 22px;
    font-weight: 300;
    line-height: 25px;
    margin-right: 30px;
}