.First-section{
    width: 100%;
    height:100vh;
    overflow-y: hidden;
}
.First-section-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    height: 100%;
}
.First-section-left-image{
    flex: 0.5;
    height: 100%;
    overflow-y: hidden;
    
}
.First-section-left-image > img{
    height: 100%;
    width: 100%;
    object-fit: fill;
}
.First-section-right-image{
    background-color: #a2454f;
    flex:0.5;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.First-section-right-image > img{
    width: 45%;
}

/* media queries */
@media screen and (max-width: 600px) {
    .First-section{
        height: 100vh;
    }
    .First-section-left-image{
        flex: 1;
        width: 100%;
    }
    .First-section-right-image{
        display: none;
    }


    .First-section-left-image > img {
        width: 100%;
    }
}
@media screen and (max-width: 660px) {
    .First-section{
        height: 100vh;
    }
    .First-section-left-image,
    .First-section-right-image{
        flex: 1;
        width: 100%;
    }
    .First-section-right-image {
        width: 100%;
    }
    .First-section-right-image > img{
        width: 45%;
    }
    .First-section-left-image > img {
        width: 100%;
    }
}